<template>
	<div class="personal-index">
		<div class="ps-msg clearfix">
			<div class="acator fl"><img src="../../../assets/images/user.png"></div>
			<div class="masge">
				<div class="name">
					<span class="b-n">{{member.name}}</span>
					<span class="u-t">初级实名用户</span>
				</div>
				<div class="msgs clearfix">
					<div class="item fl">
						<span>我的企业: </span>
						<span>|</span>
					</div>
					<div class="item fl">
						<span>手机号: {{member.mobile}}</span>
					</div>
				</div>
			</div>
		</div>
		<div class="black-ttl">
			<span>最近订单</span>
		</div>
		<div class="orderBox">
			<TheOrderList :message='message'></TheOrderList>
		</div>
	</div>
</template>

<script>
	import TheOrderList from '../TheOrderList.vue';
	export default {
		data() {
			return {
				message: [],
				member: {}
			};
		},
		components: {
			TheOrderList
		},
		created(){
			let message = []
			this.$api.personCenter.getIndexOrderList({
				page: 1,
				limit: 2,
				status: 0
			})
			.then((data)=>{
				console.log(data)
				this.message = JSON.parse(JSON.stringify(data.data.dataGrid.data))
				this.member = JSON.parse(JSON.stringify(data.data.member))
			})
		},
		methods: {
			
		}
	}
</script>

<style lang="scss" scoped>
@import '../../../assets/styles/mixin.scss';
.personal-index{
	.ps-msg{
		padding: 19px 42px;
		background-color: #FFFFFF;
		box-shadow: 0 2px 4px 0 rgba(219,219,219,.35);
		.acator{
			margin: 2px 0;
			img{
				width: 52px;
				height: 52px;
				display: block;
			}
		}
		.masge{
			padding-left: 32px;
			overflow: hidden;
			.name{
				text-align: left;
				margin-bottom: 18px;
				.b-n{
					line-height: 22px;
					font-size: 20px;
					color: $color-text;
					font-weight: 600;
				}
				.u-t{
					line-height: 16px;
					font-size: 12px;
					color: #B6BBBB;
					padding-left: 20px;
				}
				.b-n, .u-t{
					vertical-align: bottom;
				}
			}
			.msgs{
				margin-right: -20px;
				.item{
					font-size: 14px;
					line-height: 16px;
					color: $color-text;
					span{
						padding-right: 20px;
					}
				}
			}
		}
	}
	
	.black-ttl{
		margin: 16px 0;
		padding-left: 24px;
		position: relative;
		span{
			font-weight: 600;
			color: $color-text;
			font-size: 16px;
			line-height: 16px;
		}
		&:before{
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			margin-top: -3px;
			width: 14px;
			height: 3px;
			background: #1FD8D2;
			border-radius: 2px;
		}
	}
	orderBox{
		box-shadow: 0px 2px 4px 0 rgba(219,219,219,.35);
	}
}
</style>
