<template>
	<div class="orderBox">
		<div class="orderList">
			<div class="orderItem" v-for="(item,index) in message">
				<div class="orderSn clearfix">
					<span class="sn fl">订单编号: {{item.sn}}</span>
					<span class="status fr" :class="'status'+item.status">{{status(item.status)}}</span>
				</div>
				<div class="orderMsg clearfix">
					<div class="btn-price fr">
						<div class="logo"><img :src="baseUrl + item.brand_info.logo"></div>
						<div class="price">
							<span class="p">{{item.amount}}</span>
							<span class="i">元</span>
						</div>
						<div class="btns">
							<div class="btn baodan" v-if="[3,4,5].filter(i=>i===item.status).length">电子保单</div>
							<div class="btn againPay" v-if="[5,6].filter(i=>i===item.status).length">再次购买</div>
							<div class="btn toSettle" v-if="[4,5].filter(i=>i===item.status).length">申请理赔</div>
							<div class="btn toPay" v-if="item.status===0">立即支付</div>
							<div class="btn rePay" v-if="[7,8].filter(i=>i===item.status).length">重新支付</div>
							<div class="btn toDetail" @click="goDetail">查看详情</div>
						</div>
					</div>
					<div class="msg">
						<div class="ttl" v-text="item.name"></div>
						<div class="dl">被保险人：{{item.relation_info.name}}</div>
						<div class="dl">创建时间：2018-08-22 00:00:00</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		message: {
			type: Array,
			required: true
		}
	},
  data () {
    return {
			baseUrl: `${process.env.VUE_APP_RESOURCE_URL}`
    }
  },
  methods: {
    goDetail () {
      console.log(888)
      this.$router.push({
        path: '/37duc/personCenter/orderDetail/1'
      })
    },
	status(status){
		let res;
		switch(status){
			case 0: res='待支付';break;
			case 1: res='已支付';break;
			case 2: res='已承保';break;
			case 3: res='已出单';break;
			case 4: res='保障中';break;
			case 5: res='已失效';break;
			case 6: res='承保失败';break;
			case 7: res='已取消';break;
			case 8: res='支付失败';break;
		}
		return res
	}
  }
}
</script>

<style lang="scss" scoped>
	@import '@/assets/styles/mixin.scss';
	.orderBox{
		.orderList{
			.orderItem{
				background-color: #FFFFFF;
				border-bottom: 1px solid #B6BBBB;
				.orderSn{
					padding: 15px 20px 15px 0;
					margin-left: 20px;
					border-bottom: 1px solid #F1F4F4;
					.sn{
						font-size: 14px;
						color: #B6BBBB;
					}
					.status{
						font-size: 16px;
					}
					.sn, .status{
						line-height: 18px;
						height: 18px;
					}
					.status0, .status6{
						color: #FF4B4B;
					}
					.status1, .status2, .status3, .status5, .status7{
						color: #B6BBBB;
					}
					.status4{
						color: #1FD8D2;
					}
				}
				.orderMsg{
					.btn-price{
						position: relative;
						padding: 20px;
						padding-left: 0;
						.logo{
							height: 28px;
							margin-bottom: 20px;
							text-align: right;
							img{
								height: 100%;
							}
						}
						.price{
							margin: 20px 0;
							text-align: right;
							.p{
								font-size: 20px;
								line-height: 22px;
								color: $color-active-text;
							}
							.i{
								line-height: 18px;
								font-size: 12px;
								color: #D5D8D8;
							}
							.p, .i{
								vertical-align: bottom;
							}
						}
						.btns{
							margin-top: 20px;
							margin-right: -16px;
							white-space: nowrap;
							.btn{
								display: inline-block;
								margin-right: 16px;
								line-height: 34px;
								font-size: 14px;
								width: 87px;
								border-radius: 18px;
								letter-spacing: 1px;
								text-align: center;
								cursor: pointer;
								&.toDetail{
									color: #FFFFFF;
									background: #1FD8D2;
									border: 1px solid $color-active-text;
								}
								&.toPay, &.againPay, &.rePay{
									color: #FF4B4B;
									background: #FFFFFF;
									border: 1px solid #FF4B4B;
								}
								&.baodan{
									color: #A5A8A8;
									border: 1px solid #D5D8D8;
									background: #FFFFFF;
								}
								&.toSettle{
									color: #1FD8D2;
									border: 1px solid #1FD8D2;
									background: #FFFFFF;
								}
							}
						}
					}
					.msg{
						padding: 12px 20px;
						overflow: hidden;
						.ttl{
							line-height: 36px;
							font-size: 20px;
							color: $color-text;
							font-weight: 600;
							text-align: left;
							margin-bottom: 10px;
						}
						.dl{
							line-height: 30px;
							font-size: 14px;
							color: $color-text;
							text-align: left;
						}
					}
				}
			}
		}
	}
</style>
